<template>
  <div class="shoppingcar-box">
    <Head title="购物车"></Head>
    <div class="content">
      <div v-if="count>0" class="has">
        <ul>
          <li v-for="(good,i) in goodsinfo" :key="i" :id="good._id">
            <van-checkbox
              v-model="checkeds[i]"
              checked-color="red"
              class="check-one"
              @click="getPriceTotal"
            ></van-checkbox>
            <img :src="good.img" alt />
            <div class="li-r">
              <p class="li-name">{{good.goodName}}</p>
              <p class="li-goodinfo">{{good.goodInfo.color}} / {{good.goodInfo.size}}</p>
              <div class="li-b">
                <span class="li-price">￥{{good.goodPrice}}</span>
                <van-stepper v-model="values[i]" integer @change="changeCount(good._id,values[i])" />
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="empty" v-else>
        <img src="https://f6.jmstatic.com/static_cart_mobile/images/no-card-oops.png" alt />
        <p>您的购物车还没有商品哟，请先去挑选你心爱的商品吧</p>
        <button @click="gotoHome">去逛逛</button>
      </div>
    </div>
    <div class="car-foot">
      <van-checkbox v-model="checked" checked-color="red" class="check-all" @click="changeAll">全选</van-checkbox>
      <span class="del-chos"  @click="delGoods">删除选中</span>
      <span class="total">
        合计：
        <b>{{priceTatol.toFixed(2)}}</b>
      </span>
      <button class="account"  @click="gotoSuborder">去结算</button>
    </div>
  </div>
</template>

<script>
import Head from "@/components/Head.vue";

export default {
  data() {
    return {
      checkeds: [],
      count: 0,
      goodsinfo: [],
      values: [],
      checked: false,
      priceTatol: 0
    };
  },
  components: {
    Head
  },
  watch: {
    values:function(){
      this.priceTatol = 0;
      let count = 0;
      this.checkeds.forEach((element, i) => {
        if (element) {
          count++;
          this.priceTatol += this.goodsinfo[i].goodPrice * this.values[i];
        }
      });
    },
    checkeds: function() {
      this.priceTatol = 0;
      let count = 0;
      this.checkeds.forEach((element, i) => {
        if (element) {
          count++;
          this.priceTatol += this.goodsinfo[i].goodPrice * this.values[i];
        }
      });
      if (count == this.checkeds.length) {
        this.checked = true;
      } else {
        this.checked = false;
      }
    }
  },
  methods: {
    changeAll() {
      this.priceTatol = 0;
      this.checkeds.forEach((element, i) => {
        this.checkeds[i] = !this.checked;
      });
      if(!this.checked){
        this.checkeds.forEach((element, i) => {
          this.priceTatol += this.goodsinfo[i].goodPrice * this.values[i];
      });
      }
    },
    gotoSuborder(){
        let List=[]
      this.checkeds.forEach((element,i)=>{
        if(element){
            if(element){
                List.push(this.goodsinfo[i]);
            }
        }
      });
      this.$store.commit("changeOrderList",List)
      this.$router.push({name:"suborder"})
    },
    delGoods(){
      console.log(this.checkeds)
      console.log(this.goodsinfo)
      let ids={}
      this.checked.forEach((element,i)=>{
        if(element){
            
        }
      })
    },
    getPriceTotal() {
      this.checkeds.forEach((element, i) => {
        if (element) {
          this.priceTatol += this.goodsinfo[i].goodPrice * this.values[i];
        }
      });
    },
    gotoHome() {
      this.$router.push({ name: "home" });
    },
    changeCount(ids, count) {
      this.$axios
        .get("/vue/setShopcar", {
          params: {
            id: ids,
            counts: count
          }
        })
        .then(res => {
          console.log(res);
        });
    }
  },
  mounted() {
    let usernames = JSON.parse(sessionStorage.userInfo).username;
    this.$axios
      .post("/vue/getShopcarInfo", {
        username: usernames
      })
      .then(res => {
        this.goodsinfo = res.data.carList;
        this.count = res.data.total;
        console.log(this.goodsinfo);
        for (let i = 0; i < this.goodsinfo.length; i++) {
          this.checkeds.push(false);
          this.values.push(this.goodsinfo[i].count);
        }
      });
  }
};
</script>

<style lang="scss" scoped>
.shoppingcar-box {
  .content {
    padding-bottom: 2rem;
    .empty {
      text-align: center;
      padding-top: 3rem;
      img {
        height: 2rem;
        width: 2rem;
        display: inline;
      }
      p {
        font-size: 14px;
        padding: 0.4rem 0;
      }
      button {
        background: #f50800;
        border: 0;
        padding: 0.2rem 0.6rem;
        border-radius: 0.4rem;
        color: #fff;
      }
    }
    .has {
      padding: 0.2rem;
      ul {
        li {
          display: flex;
          padding: 0.2rem 0;
          border-bottom: 1px solid #eee;
          .check-one {
            margin-right: 0.3rem;
          }
          img {
            height: 2rem;
            width: 2rem;
            margin-right: 0.3rem;
          }
          .li-r {
            width: 3.6rem;
            .li-name {
              font-size: 14px;
              margin-bottom: 0.2rem;
            }
            .li-goodinfo {
              font-size: 12px;
              margin-bottom: 0.2rem;
            }
            .li-b {
              display: flex;
              justify-content: space-between;
              .li-price {
                color: #f50800;
              }
            }
          }
        }
      }
    }
  }
  .car-foot {
    background: #fff;
    border-top: #eee 1px solid;
    border-bottom: #eee 1px solid;
    width: 100%;
    box-sizing: border-box;
    font-size: 12px;
    position: fixed;
    bottom: 1rem;
    left: 0;
    // display: flex;
    padding: 0.2rem;
    .check-all {
      margin: 0.16rem 0.2rem 0 0;
      float: left;
    }
    .del-chos {
      margin: 0.2rem 0.2rem 0 0;
      float: left;
    }
    .total {
      float: left;
      display: block;
      margin-top: 0.2rem;
      b {
        color: #f50800;
      }
    }
    .account {
      float: right;
      border-radius: 1rem;
      border: 0;
      background: #ffaa00;
      padding: 0.15rem 0.6rem;
      color: #fff;
    }
  }
}
</style>